import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Link } from "react-router-dom";

function Section1({ onresponce }) {
  const handleClick = (res) => {
    onresponce(res);
  };

  return (
    <div>
      <div>
        <div className="card">
          <div className="card-header">HI NEETHU</div>
          <div className="card-body">
            <h5 className="card-title">I need to tell you something</h5>
            <p className="card-text">
              I've been thinking about something Ive never really said before,
              but I feel its the right time now. Im deeply falling for you.
              Its hard to put into words, but every moment with you feels more
              special than the last, and I cant ignore how much my feelings for
              you are growing. I just wanted you to know that you mean so much
              to me, and Im excited to see where this connection takes us.
            </p>
            <h5 className="card-title">Will you marry Me ? </h5>

            <Link to={"/success"} className="btn btn-primary first-button">
              YES
            </Link>

            <button
              style={{ margin: 20 }}
              className="btn btn-primary second-button"
              onClick={() => handleClick("NO")}
            >
              NO
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Section1;
